    // private services
    private static _baseUrl = "/api";

    private static _TOKEN = () : Promise<any> => {
        return fetch('/.auth/me', { cache: 'no-cache', credentials: 'same-origin'})
            .then( d => { 
                if ( d.status === 400) {
                    throw new Error("failed login");
                } 
                return d.json();
            });
    }
    private static _DEFAULT_GET = ( req: string ) : Promise<any> => {
        return fetch( `${DataServices._baseUrl}/${req}`, { method: 'GET', mode: 'same-origin', credentials: 'same-origin'})
            .then( d => {
                if ( !d.ok) {
                    throw new Error(d.statusText);
                }
                return d.json();
            })
            .then( j => j)
            .catch( err => console.error(`ouch ${err}`));
    }

    private _getOwner = ( id: string ) : Promise<any> => DataServices._DEFAULT_GET( `owners/${id}` );
/*        return fetch( `${DataServices._baseUrl}/owners/${id}`, { method: 'GET', mode: 'cors'})
            .then( d => d.json().then( j => { return j; }) )
            .catch( err => console.log(`ouch ${err}`));        
    } */

    private _addContact = ( id: string, fn: string, lastn: string, addr: string, city: string, state: string, zip:string, email: string, cellfone: string, workfone:string, homefone:string, primary: boolean, relations: string ) : Promise<any> => {
        const payload = JSON.stringify({
            ownerFirstName: fn,
            ownerLastName: lastn,
            ownerStreetAddress: addr,
            ownerCity: city,
            ownerState: state,
            ownerZIP: zip,
            owneremail: email,
            ownerCellPhone: cellfone,
            ownerWorkPhone: workfone,
            ownerHomePhone: homefone,
            isPrimaryContact: primary,
            relations: relations
        });
        const heads = new Headers();
        heads.append('Accept', 'application/json');
        heads.append('Content-Type', 'application/json');
        return fetch( `${DataServices._baseUrl}/owners/${id}/contacts`, {
            headers: heads,
            method: 'POST', 
            mode: 'same-origin',
            credentials: 'same-origin',
            body: payload
        }).then( d => d.json().then( j => { return j; }))
            .catch( err => console.log(`ouch ${err}`));
    }

    private _updateContact = ( id: string, cid: string, fn: string, lastn: string, addr: string, city: string, state: string, zip:string, email: string, cellfone: string, workfone:string, homefone:string, primary: boolean, relations: string ) : Promise<any> => {
        const payload = JSON.stringify({
            ownerFirstName: fn,
            ownerLastName: lastn,
            ownerStreetAddress: addr,
            ownerCity: city,
            ownerState: state,
            ownerZIP: zip,
            owneremail: email,
            ownerCellPhone: cellfone,
            ownerWorkPhone: workfone,
            ownerHomePhone: homefone,
            isPrimaryContact: primary,
            relations: relations
        });
        const heads = new Headers();
        heads.append('Accept', 'application/json');
        heads.append('Content-Type', 'application/json');
        return fetch( `${DataServices._baseUrl}/owners/${id}/contacts/${cid}`, {
            headers: heads,
            method: 'PUT', 
            mode: 'same-origin',
            credentials: 'same-origin',
            body: payload
        }).then( d => d.json().then( j => { return j; }))
            .catch( err => console.log(`ouch ${err}`));
    }

