    private _getParcel = ( pid: string, force: boolean ) : Promise<any> => {
        if ( force ) {
            return fetch( `${DataServices._baseUrl}/parcels/${pid}`, { method: 'GET', mode: 'cors', cache: "reload" })
            .then( d => d.json().then( j => { return j; }) )
            .catch( err => console.log(`ouch ${err}`));
        } else {
            return DataServices._DEFAULT_GET( `parcels/${pid}`);
    }
    }

    private _setROEStatus = ( pid: string, status: string ) => {
        const heads = new Headers();
        heads.append('Accept', 'application/json');
        heads.append('Content-Type', 'application/json');
        return fetch( `${DataServices._baseUrl}/parcels/${pid}/roe/${status}`, {
            headers: heads,
            method: 'PUT', 
            mode: 'same-origin',
            credentials: 'same-origin'
        }).then( d => d.json().then( j => { return j; }))
            .catch( err => console.log(`ouch ${err}`));        
    }

    private _setROEStatus_condition = ( pid: string, status: string, condition: string ) => {
        const heads = new Headers();
        heads.append('Content-Type', 'application/json');
        const payload = JSON.stringify({
            'statusCode' : status,
            'condition': condition
        });
        return fetch(`${DataServices._baseUrl}/parcels/${pid}/roe`, {
            headers: heads,
            method: 'PUT',
            mode: 'same-origin',
            credentials: 'same-origin',
            body: payload
        }).then( d => d.json().then( j => j))
            .catch( err => console.error(`ouch ${err}`));
    }
